import { useEffect, useState } from "react";
import React from "react";
import useWebSocket from "react-use-websocket";

const productionUrl = window.location.origin.replace("https", "wss");
const socketUrl =
  process.env.NODE_ENV === "production" ? productionUrl : "ws://127.0.0.1:3000";
console.log("socketUrl", socketUrl);

const BinanceHeatmap = () => {
  const { sendMessage, lastMessage, readyState } = useWebSocket(socketUrl, {
    onOpen: () => console.log("opened ws"),
    onError: (errEvent) => console.log("error", errEvent),
    retryOnError: true,
    onClose: (closeEvent) => console.log("ws closed", closeEvent),
    //Will attempt to reconnect on all close events, such as server shutting down
    shouldReconnect: (closeEvent) => true,
    reconnectInterval: 1000,
    reconnectAttempts: 3,
    onReconnectStop: (numAttemp) => console.log("reconnect stopped", numAttemp),
  });
  const [binanceHeatmap, $binanceHeatmap] = useState({});
  useEffect(() => {
    if (lastMessage !== null) {
      console.log("lastMessage", lastMessage);
      const json = JSON.parse(lastMessage.data);
      // console.log("binanceHeatmap", json);
      $binanceHeatmap(json);
    }
  }, [lastMessage]);

  return (
    <>
      <h1>Binance Heatmap</h1>
      <div class="row">
        <div class="column">
          <h2>24h Change</h2>
          {readyState &&
            binanceHeatmap.change24Hs?.slice(0, 10).map((contract) => {
              return (
                <h4>
                  <span>{contract.symbol}</span>
                  <span> : {contract.priceChangePercent}</span>
                </h4>
              );
            })}
        </div>
        <div class="column">
          <h2>Trading Volume</h2>
          {readyState &&
            binanceHeatmap.tradingVol24H?.slice(0, 10).map((contract) => {
              return (
                <h4>
                  <span>{contract.symbol}</span>
                  <span> : ${contract.quoteVolume}</span>
                </h4>
              );
            })}
        </div>
        <div class="column">
          <h2>Funding Rates</h2>
          {binanceHeatmap.fundingRates?.slice(0, 10).map((contract) => {
            return (
              <h4>
                <span>{contract.symbol}</span>
                <span> : {contract.fundingRate}</span>
              </h4>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default BinanceHeatmap;
